.button {
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 30px;

  cursor: pointer;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &-order {
    width: 98px;
    height: 32px;
  }

  &-order--big,
  &-send {
    width: 106px;
    height: 40px;
  }

  &-order--green {
    background-color: $color-button;
    border: none;
    &:active {
      background-color: $color-button-hover;
    }
    @include desktop-min {
      &:hover,
      &:focus {
        background-color: $color-button-hover;
      }
    }
  }

  &-order--white {
    background-color: $color-white;
    border: 2px solid $color-button-border;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:active {
      border-color: $color-button-border-hover;
    }
    @include desktop-min {
      &:hover,
      &:focus {
        border-color: $color-button-border-hover;
      }
    }
  }

  &-subscribe {
    width: 103px;
    height: 42px;
    &--green {
      background-color: $color-button;
      border: none;
      &:active {
        background-color: $color-button-hover;
      }
      @include desktop-min {
        &:hover,
        &:focus {
          background-color: $color-button-hover;
        }
      }
    }
    &--white {
      background-color: $color-white;
      border: 0.854167px solid $color-button-border;
      &:active {
        border-color: $color-button-border-hover;
      }
      @include desktop-min {
        &:hover,
        &:focus {
          background-color: $color-modal;
        }
      }
    }
  }

  &-send--white {
    background-color: $color-white;
    border: 2px solid $color-button-border;
    &:active {
      background-color: $color-button;
    }
    @include desktop-min {
      &:hover,
      &:focus {
        background-color: $color-button;
      }
    }
  }
}
