.hero__section {
  background-color: $color-background;
  padding-bottom: 50px;
  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 0;
  }
}
.hero__container {
  @media screen and (min-width: 768px) {
    position: relative;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    height: 409px;
  }

  @media screen and (min-width: 1280px) {
    height: 608px;
  }
}

.hero__wrapper {
  width: 100%;
  @media screen and (min-width: 768px) {
    width: 537px;
  }
  @media screen and (min-width: 1280px) {
    width: 502px;
    padding-top: 159px;
  }
}

.hero__title {
  margin-bottom: 28px;

  font-size: 28px;
  font-weight: 700;
  line-height: 1.17;
  color: $color-text;

  @media screen and (min-width: 768px) {
    font-size: 42px;
    line-height: 1.2;
  }
  @media screen and (min-width: 1280px) {
    font-size: 48px;
    margin-bottom: 28px;
  }
}

.hero__text {
  margin-bottom: 28px;

  font-size: 14px;
  line-height: 1.5;
  @media screen and (min-width: 768px) {
    width: 336px;
    font-size: 16px;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    width: 100%;
  }
}

.slider {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
    z-index: 99;

    position: absolute;
    right: 40px;
    bottom: 0;
    width: 320px;
    height: 320px;
    border-radius: 269px;
    border: 14px solid $color-modal;

    &:active {
      border-color: $color-button;
    }
  }
  @media screen and (min-width: 1280px) {
    width: 580px;
    height: 580px;
    border: 26px solid $color-modal;

    top: 14px;
    right: 14px;

    &:hover,
    &:focus {
      border-color: $color-button;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    &__button-image {
      width: 11px;
      height: 20px;
    }
  }

  @media screen and (min-width: 1280px) {
    &__button-image {
      width: 16px;
      height: 29px;
    }
  }
}

// 

.swiper {
  border-radius: 269px;
}

.swiper-button-prev {
  position: absolute;
  width: 34px;
  height: 28px;

  display: flex;
  align-items: center;
  justify-content: center;

  background: $color-background;
  border-radius: 8px;
  border: none;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);

  left: -25px;
  z-index: 11;

  @media screen and (min-width: 1280px) {
    width: 48px;
    height: 40px;
    left: -40px;

    .slider__button-image:hover,
    .slider__button-image:focus {
      fill: $color-button-border;
    }
  }
}
.swiper-button-prev:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.16);
}

.swiper-button-next {
  position: absolute;
  top: 50%;

  width: 34px;
  height: 28px;

  display: flex;
  align-items: center;
  justify-content: center;

  background: $color-background;
  border-radius: 8px;
  border: none;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);

  right: -25px;
  z-index: 11;

  @media screen and (min-width: 1280px) {
    width: 48px;
    height: 40px;
    right: -40px;

    .slider__button-image:hover,
    .slider__button-image:focus {
      fill: $color-button-border;
    }
  }
}
.swiper-button-next:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.16);
}

.swiper-button-prev:after,
.swiper-button-next:after {
  display: none;
}
