        (min-resolution: 288dpi),
        (min-resolution: 3dppx) {
        background-image: url(../images/customers/customers4803x.png);
      }
    }

    @media screen and (min-width: $tablet) {
      padding-top: 60px;
      padding-bottom: 120px;
      height: 544px;
      background-image: url(../images/customers/customerstab1x.png);
